/* eslint-disable import/no-anonymous-default-export */
import React, {useState} from 'react';
import './MovieRow.css';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

export default ({ title, items }) => {
    // ** Definindo o useState com -400 pra lista ja come√ßar na direita
    const [scrollX, setScrollX] = useState(0)

    const handLeftArrow = () =>{
        // ** Definindo o quanto o X vai percorrer, pegando metade do tamanho da tela do usuario
        let x = scrollX + Math.round(window.innerWidth / 2)

        if(x > 0){
            x = 0
        }
        setScrollX(x)
    }

    const handRightArrow = () =>{
        let x = scrollX - Math.round(window.innerWidth / 2) 
        //** Pegando o total de filmes no eixo X para definir um limite
        let listW = items.results.length * 150

        //**  Comparando se o tamanho da tela - o tamanho da lista √© maior que o tamanho que ela deve percorrer (X)
        if((window.innerWidth - listW) > x){
            // ** Percorrendo o eixo X e diminuindo 60 dos padding
            x = (window.innerWidth - listW) - 60
        }

        setScrollX(x)
    }

    return (
        <div className="movieRow">
            <h2>{title}</h2>
            <div className="movieRow--left" onClick={handLeftArrow}>
                <NavigateBeforeIcon style={{fontSize: 50}}/>
            </div>
            <div className="movieRow--right" onClick={handRightArrow}>
                <NavigateNextIcon style={{fontSize: 50}}/>
            </div>

            <div className="movieRow--listarea">
                <div className="movieRow--list" style={{
                    marginLeft: scrollX,
                    width: items.results.length * 150
                }}>
                    {/** Se os itens for maior que 0, faz um map dentro do map, pra mapear o resultado */}
                    {items.results.length > 0 && items.results.map((item, key) => (
                        <div key={key} className="movieRow--item">
                            <img src={`https://image.tmdb.org/t/p/w300${item.poster_path}`} alt={item.original_title} />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}